<template>
  <div style="width:100%;height:100%;">
    <div class="we">!</div>
    <div
      style="colror:#888888;font-size: 16px;margin: 0 auto;width:80%;text-align: left;margin-top: 5px;margin-bottom: 5px;"
    ></div>
    <div class="l2">
      <input
        type="text"
        class="username"
        id="username"
        v-model="loginForm.username"
        placeholder=""
      >
    </div>
    <div
      style="colror:#888888;font-size: 16px;margin: 0 auto;width:80%;text-align: left;margin-top: 5px;margin-bottom: 5px;"
    ></div>
    <div class="l3">
      <input
        type="password"
        class="password"
        id="password"
        v-model="loginForm.password"
        placeholder=""
      >
    </div>
    <div
      style="colror:#888888;font-size: 16px;margin: 0 auto;width:80%;text-align: left;margin-top: 5px;margin-bottom: 5px;"
    ></div>
    <div class="l5">
      <div>
        <input type="text" class="code">
      </div>
      <div class="givcode"></div>
    </div>
    <div class="l6">
      <div>
        <el-checkbox></el-checkbox>
      </div>
      <div>
        <button @click="login"></button>
      </div>
    </div>
    <div class="contact"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      userToken: ""
    };
  },
  created() {},
  destroyed() {},
  methods: {
    login() {
      let that = this
      if (this.loginForm.username === '' || this.loginForm.password === '') {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        })
        return false
      } else {
        //  username  token  store token 
        that.$store.dispatch('setToken', that.loginForm.username).then(() => {
          that.$router.push({path: '/'})
        }).catch(res => {
          that.$message({
            showClose: true,
            message: res,
            type: 'error'
          })
        })
      }
    }
  }
};
</script>
<style>
</style>





