<template>
  <div class="zhuinfo">
    <table>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td style="width:392px">
          <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
        <td style="width:100px" >ID</td>
        <td style="width:392px">
          <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td >
          <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td>
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
         <el-select v-model="zaiyuanstatevalue" placeholder="">
            <el-option
              v-for="item in zaiyuanstate"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:80px">
        <td></td>
        <td>
         <el-select v-model="gradevalue" placeholder="">
            <el-option
              v-for="item in grade"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-select v-model="Healthyvalue" placeholder="">
            <el-option
              v-for="item in Healthy"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select> 
        </td>
        <td style="width:100px"></td>
        <td>
         <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
        <td style="width:100px"></td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
        <td>
         
        </td>
        <td>
           <el-select v-model="bloodvalue" placeholder="" >
            <el-option
              v-for="item in blood"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </td>
      </tr>
      <tr style="height:80px">
        <td style="width:100px"></td>
        <td>
          <el-input style="width:235px" v-model="input" placeholder=""></el-input>
        </td>
