        </td>
        <td style="width:100px"></td>
        <td>
          
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
    data() {
      return {
grade:[
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      gradevalue: "",
      Healthy:[
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      Healthyvalue: "",
      zaiyuanstate:[
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      zaiyuanstatevalue: "",
      blood:[
        {
          value: "A",
          label: "A"
        },
        {
          value: "B",
          label: "B"
        },
        {
          value: "O",
          label: "O"
        },{
          value: "AB",
          label: "AB"
        },{
          value: "RhA",
          label: "RhA"
        },{
          value: "RhB",
          label: "RhB"
        },{
          value: "RhO",
          label: "RhO"
        },{
          value: "RhAB",
          label: "RhAB"
        },
      ],
      bloodvalue: "",
        
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    }
};
</script>
<style>
.zhuinfo table {
  border: none;
}
</style>




